/*
Color Variables
*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
  list-style: none; }

*:visited,
*:link,
*:active {
  color: #111517; }

*:focus {
  outline: none !important; }

.btn {
  border: 0px;
  background-color: white;
  border: 0;
  border-radius: .3rem;
  box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.4);
  cursor: pointer;
  font-family: inherit;
  font-weight: 600;
  transition: ease-in-out .5s; }
  .btn:hover {
    box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.9); }
  .btn:focus {
    box-shadow: inset 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.9); }

.visible {
  visibility: visible;
  opacity: 1; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth; }

body {
  background-color: #fafafa;
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 300; }

.container {
  height: 100vh;
  display: flex;
  flex-direction: column; }

.country-container {
  padding: 3rem;
  display: grid;
  gap: 7rem; }
  @media only screen and (max-width: 1100px) {
    .country-container {
      gap: 3rem; } }
  .country-container__back {
    width: 11.8rem; }
  .country-container__button-back {
    padding: 1.5rem 3rem;
    display: flex;
    align-items: center;
    font-size: 1.6rem; }
    .country-container__button-back::before {
      content: '\f30a';
      font-family: 'Font Awesome 5 Free';
      font-weight: 900;
      font-size: 2rem;
      padding-right: 1rem; }

.country-details {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas: "flag info";
  gap: 10rem; }
  @media only screen and (max-width: 1100px) {
    .country-details {
      grid-template-columns: minmax(10rem, 55rem);
      grid-template-areas: "flag" "info";
      justify-content: center;
      gap: 5rem; } }
  .country-details__flag {
    grid-area: flag;
    align-self: center; }
  .country-details__flagImg {
    width: 100%;
    height: 100%;
    max-height: 40rem;
    box-shadow: 0rem 0rem 1rem 0.3rem rgba(32, 44, 55, 0.5);
    object-fit: cover; }
    .country-details__flagImg--CHN {
      object-position: top; }
    .country-details__flagImg--COD, .country-details__flagImg--BTN, .country-details__flagImg--NPL {
      object-fit: fill; }
    .country-details__flagImg--CAF {
      object-position: top; }
      @media only screen and (min-width: 1556px) {
        .country-details__flagImg--CAF {
          max-height: 60rem;
          object-position: center; } }
    @media only screen and (max-width: 1400px) {
      .country-details__flagImg {
        height: auto; } }
  .country-details__info {
    grid-area: info;
    display: grid;
    grid-template-columns: 30rem 1fr;
    grid-template-rows: auto;
    grid-template-areas: ". ." "infoPartOne infoPartTwo" "infoPartThree infoPartThree";
    row-gap: 1.5rem;
    align-items: center; }
    @media only screen and (max-width: 1100px) {
      .country-details__info {
        grid-template-columns: repeat(2, 1fr);
        grid-template-areas: ". ." "infoPartOne infoPartOne" "infoPartTwo infoPartTwo" "infoPartThree infoPartThree"; } }
  .country-details__headingPrimary {
    font-size: calc(1rem + .96vw);
    white-space: nowrap; }
    @media only screen and (max-width: 1100px) {
      .country-details__headingPrimary {
        padding-bottom: 1rem;
        font-size: calc(1rem + 2.5vw);
        white-space: normal; } }
  .country-details__headingQuaternary {
    padding-right: 1rem; }

.info-item {
  display: flex;
  padding-bottom: 1rem;
  font-weight: 600; }
  .info-item:not(:last-child) {
    padding-right: 1rem; }

.info-list {
  font-size: calc(1rem + .40vw); }
  @media only screen and (max-width: 1100px) {
    .info-list {
      font-size: calc(1rem + 1vw); } }
  .info-list--partOne {
    grid-area: infoPartOne; }
  .info-list--partTwo {
    grid-area: infoPartTwo; }
  .info-list--partThree {
    grid-area: infoPartThree;
    padding-top: 2rem; }
  .info-list__domain {
    display: flex; }
  .info-list__curriences {
    display: flex;
    flex-wrap: wrap; }
  .info-list__languages {
    display: flex;
    flex-wrap: wrap; }
  .info-list__borders {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center; }
    .info-list__borders h4 {
      padding-bottom: 1rem;
      padding-right: 1rem; }
    @media only screen and (max-width: 1100px) {
      .info-list__borders {
        gap: 4rem; }
        .info-list__borders h4 {
          width: 100%; } }

.borders-item {
  padding: 0 1rem 1rem 0; }

.btn--borders {
  padding: .5rem 3rem; }
  @media only screen and (max-width: 1100px) {
    .btn--borders {
      font-size: calc(1rem + 1vw); } }
