/*
Color Variables
*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
  list-style: none; }

*:visited,
*:link,
*:active {
  color: #111517; }

*:focus {
  outline: none !important; }

.btn {
  border: 0px;
  background-color: white;
  border: 0;
  border-radius: .3rem;
  box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.4);
  cursor: pointer;
  font-family: inherit;
  font-weight: 600;
  transition: ease-in-out .5s; }
  .btn:hover {
    box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.9); }
  .btn:focus {
    box-shadow: inset 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.9); }

.visible {
  visibility: visible;
  opacity: 1; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth; }

body {
  background-color: #fafafa;
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 300; }

.container {
  height: 100vh;
  display: flex;
  flex-direction: column; }

.home {
  padding: 1.5rem; }

.scroll {
  position: fixed;
  top: 89%;
  left: 100%;
  width: 5rem;
  height: 5rem;
  background-color: white;
  box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.4);
  cursor: pointer;
  transition: ease-in-out 1s;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(-150%); }
  @media only screen and (max-width: 1100px) {
    .scroll {
      top: 85%; } }
  .scroll:hover {
    transform: translateX(-150%) scale(1.1);
    transition: ease-in-out .1s; }
  .scroll__arrowUp {
    font-size: 2rem;
    color: #111517; }

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh; }
  @media only screen and (max-width: 698px) {
    .loading {
      height: calc(30vh + 5vh); } }
  @media only screen and (max-width: 1100px) {
    .loading {
      height: 50vh; } }
  .loading__loader {
    border: 1rem solid #a7b8c8;
    border-radius: 50%;
    border-top: 1rem solid green;
    box-shadow: inset 1rem 1rem 4rem rgba(32, 44, 55, 0.2);
    width: 10rem;
    height: 10rem;
    animation: spin 1s linear infinite; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.filter {
  grid-template-columns: repeat(2, 1fr);
  padding-bottom: 3rem; }
  @media only screen and (max-width: 768px) {
    .filter {
      grid-template-columns: 1fr;
      gap: 2rem; } }
  .filter__searchPanel {
    display: flex;
    align-items: center;
    padding-left: 1.5rem;
    width: 50rem; }
    @media only screen and (max-width: 686px) {
      .filter__searchPanel {
        width: 100%; } }
  .filter__icon {
    position: relative;
    z-index: 100;
    color: #858585;
    fill: currentColor;
    width: 2rem;
    height: 2rem; }
  .filter__selectPanel {
    display: flex;
    justify-content: flex-end;
    position: relative;
    z-index: 100; }
    @media only screen and (max-width: 768px) {
      .filter__selectPanel {
        justify-content: flex-start; } }
  .filter__chooseRegion {
    width: 20rem;
    outline: 0;
    background-color: white;
    color: inherit;
    font-family: inherit;
    font-size: 1.6rem;
    font-weight: 600;
    border: 0;
    border-radius: .3rem;
    box-shadow: 1rem 1rem 4rem rgba(32, 44, 55, 0.2);
    cursor: pointer;
    padding: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-content: center;
    transition: ease-in-out 1s; }
    .filter__chooseRegion--caretDown::after {
      content: '\f0d7';
      font-weight: 900;
      font-family: 'Font Awesome 5 Free'; }
    .filter__chooseRegion--caretUp::after {
      content: '\f0d8';
      font-weight: 900;
      font-family: 'Font Awesome 5 Free'; }
  .filter__dropdown {
    width: 20rem;
    outline: 0;
    background-color: white;
    color: inherit;
    font-family: inherit;
    font-size: 1.6rem;
    font-weight: 600;
    border: 0;
    border-radius: .3rem;
    box-shadow: 1rem 1rem 4rem rgba(32, 44, 55, 0.2);
    cursor: pointer;
    margin-top: 1rem;
    opacity: 0;
    position: absolute;
    top: 100%;
    visibility: hidden;
    transition: ease-in-out .5s; }
    .filter__dropdown-item {
      padding: .5rem 1.5rem;
      display: flex;
      justify-content: space-between;
      align-content: center;
      transition: ease-in-out .5s; }
      .filter__dropdown-item:hover {
        background-color: #2b3945;
        color: white; }
      .filter__dropdown-item--all::after, .filter__dropdown-item--africa::after, .filter__dropdown-item--americas::after, .filter__dropdown-item--asia::after, .filter__dropdown-item--europe::after, .filter__dropdown-item--oceania::after {
        font-weight: 900;
        font-family: 'Font Awesome 5 Free'; }
      .filter__dropdown-item--all::after {
        content: '\f0ac'; }
      .filter__dropdown-item--africa::after {
        content: '\f57c'; }
      .filter__dropdown-item--americas::after {
        content: '\f57d'; }
      .filter__dropdown-item--asia::after {
        content: '\f57e'; }
      .filter__dropdown-item--europe::after {
        content: '\f7a2'; }
      .filter__dropdown-item--oceania::after {
        content: '\f57e'; }
