/*
Color Variables
*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
  list-style: none; }

*:visited,
*:link,
*:active {
  color: #111517; }

*:focus {
  outline: none !important; }

.btn {
  border: 0px;
  background-color: white;
  border: 0;
  border-radius: .3rem;
  box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.4);
  cursor: pointer;
  font-family: inherit;
  font-weight: 600;
  transition: ease-in-out .5s; }
  .btn:hover {
    box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.9); }
  .btn:focus {
    box-shadow: inset 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.9); }

.visible {
  visibility: visible;
  opacity: 1; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth; }

body {
  background-color: #fafafa;
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 300; }

.container {
  height: 100vh;
  display: flex;
  flex-direction: column; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  box-shadow: 1rem 1rem 4rem rgba(32, 44, 55, 0.1);
  min-height: 8.5rem;
  max-height: 8.5rem; }
  .header__logo {
    padding: 2rem 3rem;
    font-size: 2.5rem; }
  .header__darkLightMode {
    display: flex;
    align-items: center;
    padding: 0rem 3rem;
    cursor: pointer; }
  .header__change-input {
    width: 0;
    height: 0;
    opacity: 0; }
  .header__icon-moon {
    transform: rotate(90deg); }
  .header__mode-name {
    padding-left: 1rem;
    font-size: 1.3rem;
    font-weight: 800; }
