/*
Color Variables
*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
  list-style: none; }

*:visited,
*:link,
*:active {
  color: #111517; }

*:focus {
  outline: none !important; }

.btn {
  border: 0px;
  background-color: white;
  border: 0;
  border-radius: .3rem;
  box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.4);
  cursor: pointer;
  font-family: inherit;
  font-weight: 600;
  transition: ease-in-out .5s; }
  .btn:hover {
    box-shadow: 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.9); }
  .btn:focus {
    box-shadow: inset 0rem 0rem 0.5rem 0.2rem rgba(32, 44, 55, 0.9); }

.visible {
  visibility: visible;
  opacity: 1; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth; }

body {
  background-color: #fafafa;
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 300; }

.container {
  height: 100vh;
  display: flex;
  flex-direction: column; }

.countries-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 30rem));
  gap: 3rem;
  justify-content: space-between;
  align-items: center; }
  @media only screen and (max-width: 742px) {
    .countries-container {
      justify-content: center; } }
  @media only screen and (max-width: 686px) {
    .countries-container {
      grid-template-columns: repeat(auto-fit, minmax(20rem, 35rem)); } }

.countryCard {
  display: grid;
  grid-template-rows: repeat(2, minmax(10rem, 20rem));
  border-radius: 1%;
  overflow: hidden;
  background-color: white;
  box-shadow: 1rem 1rem 4rem rgba(32, 44, 55, 0.2);
  backface-visibility: hidden;
  transition: ease-in-out .2s; }
  .countryCard:hover {
    transform: scale(1.1); }
  @media only screen and (max-width: 686px) {
    .countryCard {
      grid-template-rows: 23rem minmax(10rem, 20rem); } }
  .countryCard__flag {
    grid-row: 1/2; }
  .countryCard__flagImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center; }
  .countryCard__757description {
    grid-row: 2/3;
    padding: 1rem;
    border-top: 1px solid #2b3945; }
  .countryCard__description {
    padding: 1rem;
    border-top: 1px solid #2b3945; }
  .countryCard__primary {
    font-size: 1.6rem; }
  .countryCard__itemList {
    display: flex;
    flex-direction: column;
    align-self: center;
    font-size: 1.6rem;
    font-weight: 600; }
  .countryCard__item span {
    font-weight: 800; }
  .countryCard__item:first-child {
    padding-top: 2rem; }
  .countryCard__item:not(:last-child) {
    padding-bottom: 2rem; }
